import React, { Component, ReactNode, useState } from 'react'
import { Input, Button, message, Modal } from 'antd'
import './login.css'
import acat from '../../req/requests'
import axios from 'axios'
import acatLogo from '../../assets/img/logo.jpeg'

const inputStyle = { border: 'none', borderBottom: '1px solid rgba(0,0,0,.6)', paddingLeft: '5px', borderRadius: 'none' };
const btnStyle = { backgroundColor: '#0067b8', color: 'white', padding: '5px 33px' };
const disStyle = { backgroundColor: 'rgba(8, 103, 184,.5)', color: 'white', padding: '5px 33px' }
const mailBtnStyle = { marginLeft: '10px', backgroundColor: '#0867b8', color: 'white' };
const mailDisStyle = { marginLeft: '10px', backgroundColor: 'rgba(8, 103, 184,.5)', color: 'white' };

interface Props {
    setLogin: any
}
export default class Login extends Component<Props, object> {
    state: Readonly<{ isLogin: boolean }> = {
        isLogin: true,
    }
    changeState = (prop: string, val: boolean) => {
        return () => {
            this.setState({
                [prop]: val
            })
        }
    }
    render(): ReactNode {
        return (
            <div className='login'>
                <div className="loginbox shadow-box">
                    <div className="logo_pic">
                        <img src='https://cdn.newimg.ltd/recruitSystem/img/logo.jpeg' alt="acat 的logo" title='acat-todo' className='logo_pic' />
                        <span className='bolder_name logo_name'>一只猫面试管理系统</span>
                    </div>
                    {this.state.isLogin ? <SignIn setLogin={this.props.setLogin} goUp={this.changeState('isLogin', false)} /> : <SignUp goIn={this.changeState('isLogin', true)} />}
                </div>
            </div>
        )
    }
}

interface SignInProps {
    goUp: Function,
    setLogin: any,
}

const SignIn: React.FC<SignInProps> = (props) => {
    let [userid, setId] = useState('');
    let [pass, setPass] = useState('');
    let isDisabeld = !userid || !pass;
    function login() {
        acat.login({
            data: {
                userid,
                password: pass,
            }
        }).then(() => {
            let { data, code, msg } = acat.getData('login');
            if (!code) {
                let { Token } = data;

                localStorage.setItem('access_token', Token);

                axios.defaults.headers = Object.assign(axios.defaults.headers, {
                    'access_token': Token
                });

                props.setLogin(true);

            }
            message.info(msg);
        })
    }

    let [isPassEdit, setPassEdit] = useState(false);

    return (
        <div className='sign-in'>
            <div className="heading bolder_name">
                登录
            </div>
            <div className="loginDescription">
                使用你的acat账户登录以进行2022届纳新面试管理。
            </div>

            <Input placeholder='输入你的学号' className='item_input' onChange={(e) => { setId(e.target.value.trim()) }} style={inputStyle} />
            <Input placeholder='输入你的密码' className='item_input' onChange={(e) => { setPass(e.target.value.trim()) }} style={inputStyle} type='password' suffix={
                (<span className='jump-text' onClick={() => { setPassEdit(true) }}>忘记密码？</span>)
            } />
            <div className="btn-wrapper">
                <span className='jump-text' onClick={() => {
                    props.goUp();
                }}>没有账号？注册一个 </span>
                <Button type='primary' disabled={isDisabeld} style={!isDisabeld ? btnStyle : disStyle} onClick={login}>登录</Button>
            </div>
            <Modal title='找回密码' okText='确认' cancelText='取消' visible={isPassEdit} onCancel={() => {
                setPassEdit(false);
            }}>
                <div className='info-item'>
                    <span>账号</span>
                    <div>
                        <Input placeholder='输入账号' onChange={(e) => {

                        }}></Input>
                    </div>
                </div>
                <div className='info-item'>
                    <span>验证码</span>
                    <div>
                        <Input placeholder='输入验证码' onChange={(e) => {

                        }}></Input>
                    </div>
                </div>
                <div className='info-item'>
                    <span>新密码</span>
                    <div> <Input placeholder='输入新密码' onChange={(e) => {

                    }}></Input>
                    </div>
                </div>
                <div className='info-item'>
                    <span>新邮箱</span>
                    <div> <Input placeholder='输入新邮箱' onChange={(e) => {

                    }}></Input>
                    </div>
                </div>
            </Modal>
        </div>

    )
}

interface SignUpProps {
    goIn: Function
}


const SignUp: React.FC<SignUpProps> = (props) => {

    let [email, setMail] = useState('');
    let [code, setCode] = useState('');
    let [userid, setId] = useState('');
    let [password, setPass] = useState('');
    let timer: any;

    let [codeCount, setCodeCount] = useState(60);
    let [canClickSign, setSignClick] = useState(true);
    let isDisabled = !email || !code || !userid || !password || !canClickSign;
    let [canClickMail, setClick] = useState(true);

    function sendCode() {
        if (!canClickMail) {
            return;
        }
        setClick(false);

        let mailTest = /^[\w\d]{1,}@[a-zA-Z]{1,}\.com$/;
        console.log(mailTest.test(email))
        if (!mailTest.test(email)) {
            return message.info('邮箱格式错误🙅');
        }
        acat.sendVeriCode({
            data: {
                action: '注册',
                email,
            }
        }).then(() => {
            let { code, msg, data } = acat.getData('sendVeriCode');
            if (!code) {
                timer = setInterval(() => {
                    console.log(codeCount);
                    if (codeCount <= 0) {
                        setCodeCount(60);
                        setClick(true);
                        return clearInterval(timer);
                    }
                    codeCount--;
                    setCodeCount(codeCount);
                }, 1000);
            }
            message.info(msg);
        })
    }
    function signup() {

        setSignClick(false);

        acat.signup({
            data: {
                password,
                email,
                verifyCode: code,
                userid,
            }
        }).then(() => {
            let { msg } = acat.getData('signup');

            message.info(msg);
            setTimeout(() => {
                setSignClick(true);
            }, 500);
        })
    }

    return (
        <div className='sign-in'>
            <div className="heading bolder_name">
                注册
            </div>
            <div className="loginDescription">
                使用你的acat账户注册以进行2022届纳新面试管理。
            </div>
            <Input placeholder='设置账号' className='item_input' style={inputStyle} onChange={(e) => { setId(e.target.value.trim()) }} />
            <Input placeholder='设置你的密码' className='item_input' style={inputStyle} onChange={(e) => { setPass(e.target.value.trim()) }} />

            <div className='mail-send'>
                <Input placeholder='输入你的邮箱' className='item_input' style={inputStyle} onChange={(e) => { setMail(e.target.value.trim()) }} />
                <Button onClick={sendCode} disabled={codeCount !== 60 || !email || !canClickMail} style={codeCount === 60 && email && canClickMail ? mailBtnStyle : mailDisStyle}>{codeCount !== 60 ? `(已发送)${codeCount}s` : '发送验证'}</Button>
            </div>
            <Input placeholder='输入你的验证码' className='item_input' style={inputStyle} onChange={(e) => { setCode(e.target.value.trim()) }} />
            <div className="btn-wrapper">
                <span className='jump-text' onClick={() => {
                    props.goIn();
                }}>已有账号？去登录</span>
                <Button onClick={signup} disabled={isDisabled} style={!isDisabled ? btnStyle : disStyle} >注册</Button>
            </div>
        </div>
    )
}