import { render } from '@testing-library/react';
import { Button, Input, message, Pagination, Popconfirm, Popover, Radio, Select, Table, Tooltip, Empty } from 'antd'
import React, { Component, ReactNode, useEffect, useState } from 'react'
import acat from '../../../req/requests';
import './interview.css'

const { Option } = Select;
const { TextArea } = Input;
const btnStyle = { backgroundColor: '#0067b8', color: 'white', padding: '5px 33px', marginLeft: '20px' }

const passTrans = (pass?: boolean) => {
    let res: number;
    switch (pass) {
        case true:
            res = 1;
            break;
        case false:
            res = 2;
            break;
        default:
            res = 3;
            break;
    }
    return res;
}
const transPass = (key?: number) => {
    let res: boolean | undefined;
    switch (key) {
        case 1:
            res = true;
            break;
        case 2:
            res = false;
            break;
        default:
            res = undefined;
            break;
    }
    return res;
}

interface dataType {
    name: string,
    userid: string,
    gender: boolean,
    wish?: number,
    major: string,
    phone?: string,
    email?: string,
    final_ispass?: boolean,
    first_ability?: number,
    first_attitude?: number,
    first_interview?: string,
    first_interviewer?: string,
    first_ispass?: boolean,
    first_remarks?: string,
    second_ability?: number,
    second_attitude?: number,
    second_interview?: string,
    second_interviewer?: string,
    second_remarks?: string,
    second_ispass?: boolean,
    third_ability?: number,
    third_attitude?: number,
    third_interview?: string,
    third_interviewer?: string,
    third_remarks?: string,
    third_ispass?: boolean,
    is_sign?: boolean,
}

const wishMapper = (wish: number | undefined): string => {
    let res: string;
    switch (wish) {
        case 1:
            res = '前端web';
            break;
        case 2:
            res = '后台java';
            break;
        case 3:
            res = '后台go';
            break;
        case 4:
            res = '机器学习';
            break;
        default:
            res = '暂无组别'
            break;
    }
    return res;
}

const transGender = (gender: boolean): ReactNode => {
    if (gender) {
        return <i className="iconfont icon-nan"></i>
    } else if (gender === false) {
        return <i className="iconfont icon-nv"></i>
    } else {
        return <span style={{ color: 'grey' }}>密</span>
    }
}

const passMapper = (isPass?: boolean | undefined) => {
    let resStr: ReactNode;
    switch (isPass) {
        case true:
            resStr = (<span className='pass'>通过</span>);
            break;
        case false:
            resStr = (<span className='notPass'>未通过</span>);
            break;
        default:
            resStr = (<span className='daiDin'>待定</span>);
            break;
    }
    return resStr;
}
interface Props {
    setLogin: any,
    jumpInfo?: any,
}
export { transGender }

export default class Interview extends Component<Props, object> {
    state: Readonly<{ render: 'showInfo' | 'evaluate' | 'empty', infoData: dataType }> = {
        render: 'empty',
        infoData: {
            name: '',
            userid: '',
            gender: true,
            wish: 0,
            major: '',
        }
    };
    jumpInfo = (userid: string, val: any, path?: 'showInfo' | 'evaluate' | 'empty') => {
        acat.getOtherInf({
            params: {
                userid,
            }
        }).then(() => {
            let { code, msg, data } = acat.getData('getOtherInf');
            if (!code) {
                let oldData = Object.assign(val, data);

                acat.queryInview({
                    data: {
                        similarName: val.name,
                        "page": 1,
                        "limit": 5,
                        "fields": [
                            {
                                "field": "userid",
                                "isDesc": true
                            }
                        ],
                    }
                }).then(() => {
                    let { code, msg, data } = acat.getData('queryInview');
                    if (!code) {
                        console.log(Object.assign(oldData, data[0]));
                        this.setState({
                            infoData: Object.assign(oldData, data[0]),
                            render: path ? path : 'showInfo',
                        })
                    }
                    message.info(msg)
                })

            } else {
                message.info(msg);
            }
        })
    }
    render() {

        let { render } = this.state;

        return (
            <div className='Interview'>
                <div className="more-input">
                    <ListShow setLogin={this.props.setLogin} jumpInfo={this.jumpInfo} />
                    {render === 'evaluate' ? <Evaluate infoData={this.state.infoData} /> : ''}
                    {render === 'showInfo' ? <ShowInfo infoData={this.state.infoData} /> : ''}
                    {render === 'empty' ? <NoData /> : ''}
                </div>
            </div>
        )
    }
}

const ListShow: React.FC<Props> = (props) => {
    interface listItem {
        name?: string,
        wish?: number,
        userid?: string,
    }
    let [list, setList] = useState([] as listItem[]);

    //接口变量
    let similarName = '';

    let isDesc = true;

    let wish = 0;

    let page = 1;
    let [limit, setLimit] = useState(5);

    let timer: any;

    function getList(isAlert?: boolean) {
        acat.queryInview({
            data: {
                similarName: similarName ? similarName : null,
                fields: [
                    {
                        field: 'userid',
                        isDesc
                    }
                ],
                wish: wish === 0 ? null : wish,
                page,
                limit,
            }
        }).then(() => {
            let { code, msg, data } = acat.getData('queryInview');
            if (!code) {
                console.log(data)
                setList(data.info);
            } else {
                // message.info('登录过期，重新登录');
                props.setLogin(false);
                localStorage.removeItem('access_token');
            }
            if (!isAlert) {
                message.info(msg);
            }
        })
    }


    let canSend = true;

    useEffect(() => {
        if (!canSend) {
            return;
        }
        canSend = false;
        getList();
    }, [])


    return (
        <div className='list-show'>
            <h3 className='head-title'>面试列表</h3>
            <div className="userid-input">
                <Input placeholder='搜索面试者' onChange={(e) => {
                    similarName = e.target.value;
                    clearTimeout(timer);
                    timer = setTimeout(() => {
                        getList();
                    }, 300);
                }} />
                <Button style={btnStyle}>
                    <i className="iconfont icon-sousuo"></i>
                </Button>
            </div>
            <div className="radio-item">
                <span className='input-text'>组别</span>
                <Radio.Group defaultValue={0} onChange={(e) => {
                    wish = e.target.value;
                    clearTimeout(timer);
                    timer = setTimeout(() => {
                        getList();
                    }, 1000);
                }}>
                    <Radio value={0}>
                        all
                    </Radio>
                    <Radio value={1}>
                        web
                    </Radio>
                    <Radio value={2}>
                        go
                    </Radio>
                    <Radio value={3}>
                        java
                    </Radio>
                    <Radio value={4}>
                        py
                    </Radio>
                </Radio.Group>
            </div>
            <div className="list-wrapper">
                <div className="list-head">
                    <span>姓名  </span>
                    <span className='userid'>学号 <i className="iconfont icon-shengxu"></i></span>
                    <span >意愿组别</span>
                    <span style={{ textAlign: 'center' }}>取消签到</span>
                    <span style={{ textAlign: 'center' }}>面试</span>
                </div>
                {
                    list.map((val, i) => {
                        return (
                            <Tooltip title='点击查看信息' key={i} placement='right'>
                                <div className="list-item" onClick={() => {
                                    props.jumpInfo(val.userid, val);
                                }}>
                                    <span>{val.name}</span>
                                    <span className='userid'>{val.userid}</span>
                                    <span>{wishMapper(val.wish)}</span>


                                    <div style={{ textAlign: 'center' }}>
                                        <Popconfirm title={`确定要取消${val.name}的签到吗？`} okText='是' onCancel={(e) => { e?.stopPropagation() }} cancelText='否' onConfirm={(e) => {
                                            e?.stopPropagation();
                                            acat.unsetsign({
                                                params: {
                                                    userid: val.userid,
                                                }
                                            }).then(() => {
                                                let { code, msg } = acat.getData('unsetsign');
                                                if (!code) {

                                                }
                                                message.info(msg);
                                            })
                                        }} >
                                            <i className="iconfont icon-quxiaodingdanxiao" onClick={(e) => { e.stopPropagation() }}></i>
                                        </Popconfirm>

                                    </div>
                                    <Button onClick={(e) => {
                                        e.stopPropagation();
                                        props.jumpInfo(val.userid, val, 'evaluate');
                                    }}>开始面试</Button>
                                </div>
                            </Tooltip>

                        )
                    })
                }
            </div>
            <div className="pagi-wrapper">
                <Pagination total={200} defaultCurrent={1} pageSize={limit} pageSizeOptions={[5, 10, 20, 50, 100]} onChange={(p, pageSize) => {
                    page = p;
                    setLimit(pageSize);
                    setTimeout(() => {
                        getList();
                    });
                }} />
            </div>
        </div>
    )
}

interface infoProps {
    infoData: dataType,
}

const Evaluate: React.FC<infoProps> = (props) => {


    let isFirstFinish: boolean = false;
    let { first_ability, first_attitude, first_interview, first_interviewer, first_remarks, first_ispass } = props.infoData;
    if (first_ability && first_attitude && first_interview && first_interviewer && first_remarks && first_ispass) {
        isFirstFinish = true;
    }

    let isSecondFinish: boolean = false;
    let { second_ability, second_attitude, second_interview, second_interviewer, second_remarks, second_ispass } = props.infoData;
    if (second_ability && second_attitude && second_interview && second_interviewer && second_remarks && second_ispass) {
        isSecondFinish = true;
    }

    let isThirdFinish: boolean = false;
    let { third_ability, third_attitude, third_interview, third_interviewer, third_remarks, third_ispass } = props.infoData;
    if (third_ability && third_attitude && third_interview && third_interviewer && third_remarks && third_ispass) {
        isThirdFinish = true;
    }
    let [final_ispass, setFinal] = useState(props.infoData.final_ispass);

    let userid = props.infoData.userid, name = props.infoData.name;

    //初始化表单值
    let [interRounds, setRounds] = useState(1);
    let [ability, setAbility] = useState(first_ability);
    let [attitude, setAttitude] = useState(first_attitude);
    let [interview, setInterview] = useState(first_interview);
    let [remarks, setRemark] = useState(first_remarks);
    let [isPass, setPass] = useState(first_ispass);
    let [viewer, setViewer] = useState(first_interviewer);

    let canChange = true;

    useEffect(() => {
        if (!canChange) {
            return;
        }
        canChange = false;
        if (isFirstFinish) {
            if (isSecondFinish) {
                setRound(3);
            } else {
                setRound(2);
            }
        }
    }, [])

    function setRound(rounds: number) {
        switch (rounds) {
            case 1:
                setRounds(1);
                setAbility(first_ability);
                setAttitude(first_attitude);
                setInterview(first_interview);
                setRemark(first_remarks);
                setPass(first_ispass);
                setViewer(first_interviewer);
                break;
            case 2:
                setRounds(2);
                setAbility(second_ability);
                setAttitude(second_attitude);
                setInterview(second_interview);
                setRemark(second_remarks);
                setPass(second_ispass);
                setViewer(second_interviewer);
                break;
            case 3:
                setRounds(3);
                setAbility(third_ability);
                setAttitude(third_attitude);
                setInterview(third_interview);
                setRemark(third_remarks);
                setPass(third_ispass);
                setViewer(third_interviewer);
                break;
            default:
                break;
        }
    }


    const submit = () => {
        acat.evaluate({
            data: {
                userid,
                times: interRounds,
                interview,
                attitude,
                ability,
                remarks,
                ispass: isPass,
                interviewer: viewer,
                final_ispass,
            }
        }).then(() => {
            let { msg, code } = acat.getData('evaluate');
            if (!code) {
                return message.success(msg);
            }
            message.error(msg);
        })
    }

    const changeHandler = (func: any, tans?: any) => {
        if (tans) {
            return (e: any) => {
                func(tans(e.target.value))
            }
        }
        return (e: any) => {
            func(e.target.value)
        }
    }

    return (
        <div className='evaluate'>
            <div className="input-wrapper">
                <h3 className='head-title'>评价</h3>
                <div className="input-item">
                    <div >面试者</div>
                    <div className="info-item-val">{name}</div>
                </div>
                <div className="input-item">
                    <div >面试者学号</div>
                    <div className="info-item-val">{userid}</div>
                </div>
                <div className='input-item'>
                    <span>面试轮次</span>
                    <Select style={{ width: 120 }} value={interRounds} onChange={(val) => { setRound(val) }} >
                        <Option value={1}>第一轮</Option>
                        <Option value={2}>第二轮</Option>
                        <Option value={3}>第三轮</Option>
                    </Select>
                </div>
                <div className="input-item">
                    <span className='input-text'>态度打分</span>
                    <Input placeholder='请输入分数' type='number' onChange={changeHandler(setAttitude)} value={attitude} maxLength={2} />
                </div>
                <div className="input-item">
                    <span className='input-text'>能力打分</span>
                    <Input placeholder='请输入分数' type='number' onChange={changeHandler(setAbility)} value={ability} maxLength={2} />
                </div>

                <div className="textarea-item">
                    <span className='textarea-text'>面试评语</span>
                    <TextArea showCount maxLength={120} autoSize={{ minRows: 3, maxRows: 3 }} onChange={changeHandler(setInterview)} value={interview} />
                </div>
                <div className="textarea-item">
                    <span className='textarea-text'>备注</span>
                    <TextArea showCount maxLength={120} autoSize={{ minRows: 3, maxRows: 3 }} onChange={changeHandler(setRemark)} value={remarks} />
                </div>
                <div className="input-item">
                    <span className='input-text'>面试官署名</span>
                    <Input placeholder='输入姓名' onChange={changeHandler(setViewer)} value={viewer} />
                </div>
                <div className="input-item">
                    <span className='input-text'>本面是通过</span>
                    <Radio.Group defaultValue={3} onChange={changeHandler(setPass, transPass)} value={passTrans(isPass)}>
                        <Radio value={1} >
                            是
                        </Radio>
                        <Radio value={2}>
                            否
                        </Radio>
                        <Radio value={3}>
                            待定
                        </Radio>
                    </Radio.Group>
                </div>
                <div className="input-item">
                    <span className='input-text'>最终是否通过</span>
                    <Radio.Group defaultValue={3} onChange={changeHandler(setFinal, transPass)} value={passTrans(final_ispass)}>
                        <Radio value={1}>
                            是
                        </Radio>
                        <Radio value={2}>
                            否
                        </Radio>
                        <Radio value={3}>
                            待定
                        </Radio>
                    </Radio.Group>
                </div>
            </div>
            <div className="btn-item">
                <Button style={btnStyle} onClick={submit} >提交</Button>
            </div>
        </div>
    )
}


